<template>
  <div class="box">
    <div
      v-for="(item, index) in hotsongs"
      :key="index"
      @click="$emit('playsong', item), $emit('musiclist', hotsongs)"
    >
      <div>
        <span class="num"> {{ String(index + 1).padStart(2, "0") }}</span>
      </div>
      <div class="gray">
        <div>
          <p>{{ item.name }}</p>
          <p>
            <span class="bg2"></span>{{ item.ar[0].name }}-{{ item.al.name }}
          </p>
          <div v-if="item.id !== songId" class="newsong-play"></div>
          <div v-else class="newsong-play active" :class="{ playing: playing }">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:{
    songId:Number,
    playing:Boolean
  },
  data: function () {
    return {
      hotsongs: [],
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        console.log(res);
        let n = res.data.playlist.tracks;
        console.log(res.data.playlist);
        for (let i = 0; i < 20; i++) {
          this.hotsongs.push(n[i]);
        }
      });
  },
};
</script>

<style lang="less" scoped>
.box {
  
  > div {
    display: flex;
    align-items: center;
    font-size: 19rem;
    .num{
      font-weight: 300;
      color: gray;
      margin: 0 10rem;
    }
  }
  > div:nth-child(1) {
    span {
      color: red;
    }
  }
  > div:nth-child(2) {
    span {
      color: red;
    }
  }
  > div:nth-child(3) {
    span {
      color: red;
    }
  }
  .gray {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6rem 5rem 10rem 0;
    >div{
      width: 100%;
      position: relative;

    }
    // margin: 5rem 0;
    border-bottom: 1rem solid rgba(128, 128, 128, 0.082);
    div > p:nth-of-type(2) {
      font-size: 12rem;
      color: gray;
    }
  }
  .bg2 {
    margin: 0;
    display: inline-block;
    width: 24rem;
    height: 8rem;
    color: white;
    background-size: 740%;
    background-repeat: no-repeat;
    background-position: 2rem 0rem;
    background-image: url("../assets/1.png");
  }
   @keyframes playingrunning {
    from {
      transform: rotateX(15deg);
    }
    to {
      transform: rotateX(60deg);
    }
  }
  .newsong-play {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    transform: translateY(-50%);
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>