    </svg>
    <!--  -->
    <SearchName
      :searcheds="searcheds"
      :sou="ss"
      :bool="bool"
      :abc="value"
      @cvalue="queding($event), (value = $event)"
      @v-value="queding($event), (value = $event)"
      v-if="!searchmusic"
      :arr="arr"
      @del="del($event)"
    />
    <!--  -->
    <SearchAlbum :album="album" v-if="searchmusic" />
    <!--  -->
    <SearchMuisc
      v-for="item in searchmusic"
      :key="item.id"
      :item="item"
      @playsong="$emit('playsong', $event), $emit('musiclist', searchmusic)"
      :songId="songId"
      :playing="playing"
    />
  </div>
</template>

<script>
// import SearchInput from "@/components/SearchInput";
import SearchName from "@/components/SearchName";
import SearchMuisc from "@/components/SearchMuisc";
import SearchAlbum from "@/components/SearchAlbum";
import vue from 'vue'
import _ from 'lodash'
// import { get } from "https";
vue.prototype._=_
export default {
  components: {
    // SearchInput,
    SearchName,
    SearchMuisc,
    SearchAlbum,
  },
  props: {
    songId: Number,
    playing:Boolean
  },
  data: function () {
    return {
      searcheds: [],
      ss: [],
      value: "",
      searchmusic: null,
      arr: JSON.parse(localStorage.getItem("arr")) || [],
      album: null,
      loading: false,
      // arr:[],
      // bool:null
    };
  },
  computed: {
    bool() {
      return this.value == "";
    },
    options: function () {
      return {
        keywords: this.value,
        type: "mobile",
      };
    },
  },
  // function name(params) {
    
  // }
  methods: {
    
    queding(keywords) {
      //  this.axios
      // console.log(value);
      // 
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/multimatch", {
          params: {
            keywords,
          },
        })
        .then((res) => {
          this.loading = false;
          this.album = res.data.result;
          console.log(res);
        });
      this.axios
        // 
        .get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords,
            limit: 20,
          },
        })
        .then((res) => {
          this.loading = false;
          console.log(res);
          this.searchmusic = res.data.result.songs;
        });
      // });
      this.arr = [...new Set([keywords, ...this.arr])];
    },
    del($event) {
      console.log($event);
      this.arr = this.arr.filter((e) => e !== $event);
    },
    getSelect(n){
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: n,
        })
        .then((res) => {
          console.log(res);
          this.ss = res.data.result.allMatch;
        });
    }
  },
  created() {
    // 
    this.loading = true;
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.loading = false;
      this.searcheds = res.data.result.hots;
    });
    this.debouncedGetSearchSuggests = _.debounce(this.getSelect, 800);
  },
  watch: {
    options: function (n) {
      // console.log(n);  
    
      if (n.keywords) {
  
        // 
      this.debouncedGetSearchSuggests(n)
   
      } else {
        this.ss = [];
      }   
    },
    arr: function (newarr) {
      localStorage.setItem("arr", JSON.stringify(newarr));
    },
  },
};
</script>

<style lang="less" scoped>
