<template>
  <div>
    <ul v-if="item" @click="$emit('playsong',item)">
      <li class="gray">
        <div>
          <p>{{ item.name }}</p>
          <span class="bg"></span><span>{{ item.ar[0].name }}</span> -<span>{{
            item.al.name
          }}</span>
        </div>
        <div v-if="item.id !== songId" class="newsong-play"></div>
          <div v-else class="newsong-play active" :class="{ playing: playing }">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    item: Object,
    songId:Number,
    playing:Boolean
  },
};
</script>

<style lang="less" scoped>
li {
  margin-bottom: 5rem;
  p {
    font-size: 16rem;
  }
  .bg {
    display: inline-block;
    width: 24rem;
    height: 8rem;
    background-size: 740%;
    background-repeat: no-repeat;
    background-position: 4rem 0rem;
    background-image: url("../assets/1.png");
  }
}
.gray {
  color: #507daf;
  height: 55rem;
  font-size: 12rem;
  margin: 0 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
  div:nth-of-type(1) {
    width: 90%;
    overflow: hidden;
    p {
      margin: 0;
      white-space: nowrap;
    }
    span {
      color: gray;
    }
  }
}
.bofang {
  width: 22rem;
  height: 22rem;
  background-size: 750%;
  z-index: 999;
  background-image: url("../assets/1.png");
  background-position: -24rem 0;
}

.newsong-play {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    transform: translateY(-50%);
    @keyframes playingrunning {
    from {
      transform: rotateX(15deg);
    }
    to {
      transform: rotateX(60deg);
    }}
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
</style>